    //----------------------------
    //  bis-menu
    function OutputBisMenuList() {

        const [outputBisMenuList, set_outputBisMenuList] = useState(null);

        useEffect(() => {
            BisGlobFunctions.globCorpIndication_outputInRedux();
        }, []
        );

        useLayoutEffect(() => {
            refreshBisMenuList();
        }, []
        );

        //      
        useEffect(() => {
            // console.log("===   ");
            if (ref_projectsMenuContenner.current) {
                //     ,        .    -   ,                 
                // console.log("last_offsetHeight_OutputBisMenuList= " + last_offsetHeight_OutputBisMenuList);

                ref_promezhutochnContennerForOutputBisMenuList.current.style.minHeight = last_offsetHeight_OutputBisMenuList + "px";

                //       
                ref_projectsMenuContenner.current.scrollTop = scrollPosition_projectsMenuContenner;


                //    
                setTimeout(() => {
                    //         
                    if (ref_OutputBisMenuList.current) {
                        //   
                        last_offsetHeight_OutputBisMenuList = ref_OutputBisMenuList.current.offsetHeight;
                        //      
                        ref_promezhutochnContennerForOutputBisMenuList.current.style.minHeight = last_offsetHeight_OutputBisMenuList + "px";
                    }
                    //         
                    GLOB_DATA.checkOverflow_BisMenuList();
                }, 5);
            }
        });


        function refreshBisMenuList() {
            let bisMenuList = null;
            if (
                (bisDataRedux) && (bisDataRedux.projects) && (bisDataRedux.projects.length > 0)
                && corpAccount_Redux
            ) {
                bisMenuList = bisDataRedux.projects.filter(item => (
                    item.parentCorpAccount_ID === corpAccount_Redux.corpAccount_ID
                    &&
                    item.defaultAdminForThisProject === corpAccount_Redux.defaultAdminForThisProject
                ));

                if (bisMenuList) {
                    bisMenuList = bisMenuList.map(
                        (item, index) => (
                            <BisListMenuSingleContenner
                                key={item.project_ID}
                                itemText={item.projectSettings.project_Name}
                                project_ID={item.project_ID}
                                //     MAP     -        ,    
                                currentIndexArray={BisGlobFunctions.find_Project_index_in_BD(item.project_ID)}
                                activeStatus={(activeBisProject_INDEX_FromRedux === index) ? true : false}
                                item_by_Props={item}
                                corpAccount_ID={item.parentCorpAccount_ID}
                                m_delClick={() => {
                                    my_dispatch({ type: "SET_isDialogVisible", new_isDialogVisible: true });
                                    my_dispatch({ type: "SET_carrentActiveSubdialog", new_carrentActiveSubdialog: "currentDialigFormIs_confirmDeleteOneProject" });
                                }}
                            />
                        )
                    );
                }
            }
            set_outputBisMenuList(bisMenuList);
        }

        return (
            <div className='OutputBisMenuList'
                ref={ref_OutputBisMenuList}
                style={{
                    display: "flex",
                    flexDirection: "column",
                    flexWrap: "nowrap",
                    width: "100%", // ,     
                    height: "fit-content",
                    paddingRight: isProgectsBlockOverflow ? "6px" : 0, //         

                    //  border: "solid 3px red",
                }}

                //     
                onScroll={() => {
                    // scrollPositionMenu = ref_OutputBisMenuList.current.scrollTop;

                }}
            >
                {outputBisMenuList}

            </div>
        )
    }

