        //  console.log(" find_projectIndex_inLocal_BD")// ;
        //  console.log("chatListRedux= ");
        //  console.log(chatListRedux);
        let findIndex = null;
        findIndex = bisDataRedux.projects.findIndex(item => item.project_ID === project_ID);
