function TeamCurrentSubProject(props) {
    const bisDataRedux = useSelector(mState => mState.m_Redus_bisData.bisDataRedux);

    let teamList = "";

    if ((props.project_INDEX != null && props.project_INDEX >= 0)
        && (props.subProject_INDEX != null && props.subProject_INDEX >= 0)
        && bisDataRedux.projects[props.project_INDEX].subProjects[props.subProject_INDEX].subProjectSettings.teamList_ofResponsible_subProject
    ) {
        teamList = bisDataRedux.projects[props.project_INDEX].subProjects[props.subProject_INDEX].subProjectSettings.teamList_ofResponsible_subProject.map(
            (item, index) => (
                <div className="output_TeamCurrentSubProject"
                    key={Math.random()}
                >
                    <div className="arrayField_User"
                        style={{
                            position: "relative",
                            margin: "2px",
                        }}

                        title={item.user_Email}
                    >
                        <div className="userAvatar"
                            style={{
                                width: "24px",
                                height: "24px",
                                borderRadius: "50%",
                            }}
                        >
                        </div>

                        <div className="userOnlineStatus"
                            style={{
                                position: "absolute",
                                width: "6px",
                                height: "6px",
                                borderRadius: "50%",
                                left: 0,
                                bottom: 0,
                            }}
                        >
                        </div>
                    </div>
                </div>
            )
        )
    }

    // console.log("====== teamList= ");
    //  console.log(teamList);

    return teamList;
}

